import * as React from "react";
import Navigation, { NavigationItem } from "src/components/Navigation/Navigation";
import Banner from "src/components/Banner";
import styled from "styled-components";

export interface JoyLandProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        display: flex;
        margin-bottom: 60px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;
            font-size: 18px;
        }
        & > div:nth-child(2) {
            width: 80%;
            line-height: 1.71;
            font-size: 14px;

            & > p:nth-child(1) {
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;

                &.title {
                    font-size: 32px;
                    font-weight: bold;

                    margin-bottom: 34px;
                }

                &.subTitle {
                    font-size: 22px;
                    font-weight: bold;

                    margin-bottom: 10px;
                }
            }
        }
    }
`;

export default class JoyLand extends React.Component<JoyLandProps, any> {
    public render() {
        return (
            <>
                <Navigation list={[{ name: "홈", link: "/" }, { name: "브랜드", link: "/brand/" }, { name: "조이랜드", link: "/joyland" }]}>
                    <NavigationItem link="http://www.joylandenglish.com">Joyland 홈페이지 바로가기</NavigationItem>
                </Navigation>
                <Content>
                    <Banner>
                        <img src={require("../../assets/brand/joyland/joyland-banner.png")} alt="" />
                        <div className="desc typing">
                            <h1 lang="en">Joyland</h1>
                            <h2>언어와 문화로 사람을 잇다</h2>
                        </div>
                    </Banner>
                    <Desc>
                        <div>
                            <div />
                            <div>
                                <p className="title">편안하고 재미있게 배우는 영어회화</p>
                                <img
                                    src={require("../../assets/brand/joyland/img1.png")}
                                    style={{ width: "90%", height: 467, backgroundColor: "#e6e6e6", marginBottom: 34 }}
                                    alt=""
                                />
                                <p>
                                    주식회사 유니언플레이스의 투자를 받아 문을 연 글로벌컬쳐 브랜드 조이랜드 잉글리시카페. 당산역 3번 출구에
                                    위치한 동화빌딩 2층으로 들어서자, 유니크한 공간이 눈앞에 펼쳐졌다. 한 눈에 다 들어오지 않는 넓은 카페,
                                    탁 트인 전망, 모던 프렌치한 테이블과 의자들.. 커피 한잔씩을 테이블에 놓은 사람들이 삼삼오오 모여서
                                    즐겁게 웃고 떠드는데, 바로 영어였다. 조이랜드는 영어회화라는 취미와 목표를 가진 사람들이 모여서, 멤버인
                                    ‘조이랜더’가 되고, 멤버들끼리 만나 영어로 사교모임도 하고 영어로 대화도 하는 커뮤니티 라운지 카페이다.
                                    마재영(Joey) 대표를 만나 조이랜드의 브랜드 스토리를 들어봤다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div>
                                <img
                                    src={require("../../assets/brand/joyland/img2.png")}
                                    style={{ width: "100%", height: "auto", backgroundColor: "#e6e6e6", marginBottom: 34 }}
                                    alt=""
                                />
                            </div>
                            <div>
                                <p className="subTitle">Q. 브랜드의 메인 스토리 및 계기?</p>
                                <p>
                                    캐나다 시민권자였던 마재영대표(Joey)는 한국에 돌아온 후 학원에서 영어를 가르쳤다. 그런데, 막상 학생들을
                                    가르쳐보니 대다수의  학생들이 영어를 시험을 위한 학문으로 인식하고 있었다. 이러한 생각은 자연스레 독해,
                                    듣기에만 편중된 학습으로 이어졌고 영어회화에 대한 어려움으로 연결되었다. 조이는 한국의 영어 학습문화에
                                    실증을 느끼던 차에 같은 어려움을 겪던 아나운서 제니를 만나게 되었다.  그리고 제니의 어려움을 듣고 나서는
                                    기존의 방식과는 다른 방식으로 제니의 영어 실력을 향상시켜 주기로 마음 먹었다. 조이는 제니에게 자신의
                                    외국인 친구들을 소개시켜줌으로써 그들과 대화하는 환경을 만들어 주었다. 이러한 방식은 무거운 학습
                                    분위기에서 벗어나  영어회화를 즐겁게 이야기하고 새로운 사람을 만나는 긍정적인 수단으로 인식하게 했다. 그
                                    결과 제니는 짧은 기간에도 불구하고 비약적인 발전을 이뤄냈다. 이 경험을 토대로 조이와 제니는 영어 회화에
                                    관심이 있는 사람들끼리 모여 이야기하는 커뮤니티 공간을 만들어야겠다고 생각했고 그 결과 조이랜드가
                                    탄생하였다.
                                </p>
                            </div>
                        </div>
                        <div style={{ marginTop: "-20px" }}>
                            <div />

                            <div>
                                <p className="subTitle">Q. 한국 사람들의 영어?</p>
                                <p>
                                    한국에서 영어를 배우려고 할 때 사람들은 일반적으로 학원, 온라인 강의, 미드 시청을 위주로 하더라. 세
                                    가지의 취약점을 분석하기 시작했다. 학원은 빠지면 보충이 어렵고, 정해진 시간에 가야하기 때문에 바쁜
                                    현대인들에게는 맞지 않는다는 생각이 들었다. 온라인 강의는 들을 때만 이해가 될 뿐 사실상 말을 하지 못
                                    하는 경우가 다반수였고, 미드 역시 실생활에 쓰기 좋은 다채로운 표현들이 많지만 정확히 그 상황이 되지 않는
                                    이상 쓰기 어렵다는 판단이 있었다.
                                    <br />
                                    <br />
                                    물론 위의 3가지 방법을 통해서도 스피킹 실력을 향상시키는 사람도 있다. 그러나 대다수는 내가 영어로 말을
                                    걸었을 때는 버퍼링이 걸리기 일쑤였다. 결국 말을 하기 위한 리딩, 말을 하기 위한 리스닝이 필요하다는 걸
                                    깨닫고 나서, 제니에게 시도한 학습 방식을 구체화하면서 철저히 스피킹에 초점을 맞춘 커리큘럼을 짜기
                                    시작했다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="subTitle">Q. 조이랜드 스피킹 커리큘럼?</p>
                                <p>
                                    책과 어플리케이션은 내용의 완성도를 높이기 위해 MIT공대 연구진, 국내 영문학 박사와 협업했다. 긴 연구
                                    끝에 ‘영어 스피킹’에 최적화된 조이랜드만의 교재와 어플리케이션을 개발했고 실제 멤버들이 단기간에 큰
                                    효과를 보고 있다
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="subTitle">Q. 다른 영어 관련 학원 혹은 카페와의 차별성?</p>
                                <p>
                                    조이랜드는 스피킹 실력을 향상시키기 위한 전략으로 크게 2가지 차별점을 두었다. 바로 친밀감 형성과
                                    접근성이다. 타 잉글리시 카페를 답사해보면서 공통적으로 느낀 문제는 모든 대화가 교과서적이었다. 또한
                                    영어회화 초보인 사람들은 커뮤니티를 폭 넓게 활용하지 못하고 소외되기 일쑤였다. 그래서 조이랜드는 맴버쉽
                                    제도를 통해 맴버들이 자연스럽게 친분을 쌓도록 한 후 서로의 일상적인 이야기를 영어로 대화하도록 유도하고
                                    있다. 새로운 사람을 만나고 또 서로의 일상, 문화를 공유하면서 자연스럽게 영어로 대화할 수 있는 환경을
                                    조성해준다.
                                    <br />
                                    <br />또 다른 차별점은 접근성이다. 조이랜드만의 영어 학습 교재인 ‘스피킹 가이드’와 스마트폰 앱 ’조이
                                    패스’를 통해 언제 어디서나 스피킹 학습이 가능하게 만들었다. 즉, 학원에서만 이뤄지던 학습을 시간과 장소에
                                    구애받지 않고 스스로 자유롭게 할 수 있다는 것이다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="subTitle">Q. 조이랜드 이용방법?</p>
                                <p>
                                    멤버쉽을 구매하여 조이랜드 멤버가 되면 조이랜드의 모든 서비스를 이용할 수 있다. 멤버쉽은 1,3,6개월 또는
                                    1년 단위로 구매할 수 있다. 또한 회원들에게 조이랜드안에서 통용되는 포인트를 멤버쉽 기간에 맞게 지급한다.
                                    이 포인트는 핸드폰 어플리케이션을 통해 조이랜드  안에서 현금처럼 이용 가능하다. 즉, 핸드폰 만 있으면
                                    조이랜드의 모든 서비스를 지갑 없이도 편리하게 이용할 수 있는 것이다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="subTitle">Q. 앞으로의 조이랜드?</p>
                                <p>
                                    ‘영어회화를 하고싶은 사람들이 영어로 말하고 문화를 나누는 문화공간을 제공한다’ 는 철학를 바탕으로
                                    브랜드를 확장시키는 것이 우리의 궁극적인 목표이다. 향후 5년 내로는 이 문화와 시스템을 해외로까지
                                    수출한다는 계획을 가지고 있다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="subTitle">Q. 조이랜드의 궁극적인 미션?</p>
                                <p>
                                    조이랜드는 단순히 영어회화를 배우는 것을 넘어서, ‘언어와 문화로 사람을 잇는다’는 문화적 접근으로 미션을
                                    설정했다. 언어를 학문으로 받아들이기 이전에 말하기 위한 수단으로, 인간관계를 폭넓게 하는 매개체로
                                    생각하는 것이다.
                                    <br />
                                    <br />
                                    영어를 점수화하기 바쁜 한국의 영어문화 자체를 바꾸고, 영어가 사람을 이어주는 즐거운 수단으로 인식될 수
                                    있도록 조이랜드의 이름을 걸고 최선을 다해 노력할 것이다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <iframe
                                width="100%"
                                height="430px"
                                src="https://www.youtube.com/embed/69La9vXkDyc"
                                style={{ marginTop: 10, marginBottom: 10 }}
                            />
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



