import styled, { keyframes } from "styled-components";

const typing = keyframes`
from { width: 0 }
  to { width: 100% }
`;

// const blinkCaret = keyframes`
// from, to { border-color: transparent }
//   50% { border-color: black; }
// `;

// TODO: Typoing Animation

const Banner = styled.div`
    position: relative;
    margin-bottom: 59px;
    width: 100%;
    line-height: 0.85;
    & > img {
        width: 100%;
    }
    & > div.desc {
        position: absolute;
        top: 47px;
        left: 16%;
        white-space: nowrap;
        overflow: hidden;

        & > h1 {
            width: fit-content;
            font-size: 44px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            color: #000000;
        }

        & > h2 {
            width: fit-content;
            font-size: 24px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            color: #000000;
        }

        &.typing {
            & > * {
                animation-fill-mode: forwards;
                animation: ${typing} 2s steps(40, end);
            }
            & > *:nth-child(2) {
                animation-delay: 2s;
            }
            & > *:nth-child(3) {
                animation-delay: 4s;
            }
        }
    }
`;

export default Banner;



